* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html{
  height: 100%;
}
body{
  background-image: url(https://c.wallhere.com/photos/b6/b0/1920x1200_px_Deep_Sea_Sea_Monsters_underwater-519552.jpg!d);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  background-attachment: fixed;
  overflow-x:hidden;
  height: 100%;
}
.container{
  display: grid;
  grid-template-columns: 45% 55%;
  grid-template-rows: auto auto;
  grid-template-areas: 
  "header header"
  "editor preview";
  row-gap: 50px;
}
#header{
  grid-area: header;
  font-family: 'Roboto Condensed', Sans-Serif;
  font-size: 55px;
  letter-spacing: 1vw;
  color:rgb(255, 136, 0);
  text-align: center;
  padding-top: 30px;
}
.small-container{
  font-family: 'Cabin', Sans-Serif;
  font-size: 16px;
  background-color: rgb(0,0,0, 0.5);
  /* offset-x | offset-y | blur-radius | spread-radius | color */
  box-shadow: 5px 5px 15px 5px rgb(0, 0, 20, 0.5), -5px -5px 15px 5px rgb(0, 0, 20, 0.5);
  border-radius: 6px;
  margin-right: 5%;
  margin-left: 5%;
  color:rgb(255, 136, 0);
}
#editor-container{
  grid-area: editor;
  height: max-content;
}
#preview-container{
  grid-area: preview;
  height: max-content;
  min-height: 60vh;
  width: auto;
}
#preview{
  padding: 20px;
  font-size: 20px;
  word-wrap: break-word;
}

.my-h2{
  padding: 5px 0 5px 25px;
  box-shadow: 0 5px 15px 5px rgba(0, 0, 20, 0.5);
  border-bottom: solid 1px rgb(255, 136, 0);
  
}
textarea{
  background-color: rgb(255, 255, 255, 0);
  padding: 20px;
  font-family: 'Cabin', Sans-Serif;
  font-size: 20px;
  width: 100%;
  height: 60vh;
  max-width: 100%;
  color:rgb(255, 136, 0);
  border: none;
  resize:none;
}
pre{
  background-color: rgb(255,255,255, 0.8);
  padding: 20px;
  border-radius: 10px;
  margin: 5px;
  color:rgba(0, 0, 20);
  word-wrap: break-word;
  white-space: pre-wrap; 
}
blockquote{
  margin-left: 20px;
  padding-left: 10px;
  border-left: solid 3px rgb(255, 136, 0);
  font-size: 20px;
  margin: 10px;
  font-style:italic;
} 
ol{
  padding-left: 50px;
  margin: 10px;
}
img{
  max-width: 100%;
}
#bubbles{
  display: flex;
  position: fixed;
  width: 100%;
  align-items: center;
  justify-content: space-around;
  z-index: -1;
  top: -50px;
}
.bubble{
  background-image: url('/src/bubbles.png');
  background-repeat: no-repeat;
  background-size: contain;
  height: 30px;
  animation: bubble linear infinite;
}
.bubble:nth-child(1){
  animation-delay: 2s;
  animation-duration: 12s;
  width: 25px;
}
.bubble:nth-child(2){
  animation-delay: 7s;
  animation-duration: 10s;
  width: 20px;
}
.bubble:nth-child(3){
  animation-delay: 3s;
  animation-duration: 17s;
  width: 35px;
}
.bubble:nth-child(4){
  animation-delay: 1s;
  animation-duration: 15s;
  width: 30px;
}
.bubble:nth-child(5){
  animation-delay: 4s;
  animation-duration: 7s;
  width: 15px;
}
.bubble:nth-child(6){
  animation-delay: 6s;
  animation-duration: 10s;
  width: 20px;
}

@keyframes bubble{
  0%{
    opacity: 0;
    transform: translateY(100vh);
  }
  50%{
    opacity: 1;
  }
  70%{
    opacity: 1;
  }
  100%{
    opacity: 0;
    transform: translateY(0);
  }
}

@media only screen and (max-width: 1180px) {
  .container{
    grid-template-columns: 100%;
    grid-template-rows: auto auto auto;
    grid-template-areas: 
    "header"
    "editor"
    "preview";
    row-gap: 30px;
  }
  textarea{
    min-width: 100%;
  }
}
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: rgba(0, 0, 40, 0.3);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 20);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(255, 136, 0);
}